import React from 'react';
import "../css/navbar.css";
import { Link, NavLink } from "react-router-dom";
const Navbar = () => {
    return (
        <nav>
            <ul>
                <li><NavLink to="/Home"><i class="fa-solid fa-house"></i>Home</NavLink></li>
                <li><NavLink to="/Login"><i className="fa-regular fa-circle-user"></i>Login</NavLink></li>
                <li><NavLink to="/Signup"><i className="fa-solid fa-right-to-bracket"></i>Signup</NavLink></li>
                <li><NavLink to="/Calculator"><i className="fa-solid fa-calculator"></i>Calculator</NavLink></li>
                <li><NavLink to="/Emplist"><i className="fa-solid fa-list-ul"></i>Employlist</NavLink></li>
                <li><NavLink to="/Pagination"><i class="fa-solid fa-pager"></i>Pagination</NavLink></li>
                <li><NavLink to="/Dummy">Dummy</NavLink></li>


            </ul>



        </nav>
    )
}
export default Navbar;